<template>
  <div>
    <h1>Notas</h1>

    <form @submit.prevent="editarNota(notaEditar)" v-if="editar">
      <h3>Editar nota</h3>
      <input type="text" placeholder="Nombre" v-model="notaEditar.nombre" />
      <input
        type="text"
        placeholder="Descripcion"
        v-model="notaEditar.descripcion"
      />
      <button type="submit">Editar</button>
      <button type="submit" @click="editar = false">Cancelar</button>
    </form>

    <form @submit.prevent="agregarNota()" v-if="!editar">
      <h3>Agregar nueva nota</h3>
      <input type="text" placeholder="Nombre" v-model="nota.nombre" />
      <input type="text" placeholder="Descripcion" v-model="nota.descripcion" />
      <button type="submit">Agregar</button>
    </form>

    <table>
      <thead>
        <tr>
          <td>#</td>
          <td>First</td>
          <td>last</td>
          <td>Acciones</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in notas" :key="index">
          <th>{{ item._id }}</th>
          <td>{{ item.nombre }}</td>
          <td>{{ item._descripcion }}</td>
          <td>
            <button @click="eliminarNota(item._id)">Eliminar</button>
            <button @click="activarEdicion(item._id)">Editar</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "Notas",
  data() {
    return {
      notas: [],
      nota: { nombre: "", descripcion: "" },
      editar: false,
      notaEditar: {},
    };
  },
  methods: {
    listasNotas() {
      this.axios
        .get("/nota")
        .then((res) => {
          //res es el objeto que trae la info
          console.log(res.data); //al colorar el .data me pinta solo el array con los datos
          this.notas = res.data; //Asi llevo el array a la data notas
        })
        .catch((e) => {
          console.log(e.response); //asi maneja los errores axios, con el response
        });
    },
    agregarNota() {
      this.axios
        .post("/nueva-nota", this.nota)
        .then((res) => {
          // Agrega al inicio de nuestro array notas
          this.notas.push(res.data);
          this.nota.nombre = "";
          this.nota.descripcion = "";
        })
        .catch((e) => {
          console.log(e.response.data.error.errors.nombre.message);
        });
    },
    eliminarNota(id) {
      console.log(id);
      this.axios.delete(`/nota/${id}`).then((res) => {
        const index = this.notas.findIndex((item) => item._id === res.data._id); //encuentro el id y lo guardo si es igual al que llega
        this.notas.splice(index, 1); //con esto elimino
      });
    },
    activarEdicion(id) {
      this.editar = true;
      this.axios
        .get(`/nota/${id}`)
        .then((res) => {
          this.notaEditar = res.data;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },
    editarNota(item) {
      this.axios
        .put(`/nota/${item._id}`, item) //esto me da la opcion que no este modificado
        .then((res) => {
          const index = this.notas.findIndex((n) => n._od === res.data._id);
          this.notas[index].nombre = res.data.nombre;
          this.notas[index].descripcion = res.data.descripcion;
          this.editar = false;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },
  },
  created() {
    this.listasNotas();
  },
};
</script>

